import React from 'react';
import { CaseStudy, Challenge, TextContent, ImgContent } from '..';

import audience_flow from './audience_flow.jpg';
import chart_research_table from './chart_research_table.jpg';
import charts_design from './charts_design.jpg';
import context from './context.jpg';
import freeze from './freeze.jpg';
import layout from './layout.gif';
import pie_chart_types from './pie_chart_types.jpg';
import radar_chart from './radar_chart.jpg';
import mock_analysis from './mock_analysis.jpg';
import mock_conversation from './mock_conversation.jpg';
import mock_list from './mock_list.jpg';

import legacy_analysis from './legacy_analysis.jpg';
import legacy_audience from './legacy_audience.jpg';



const DataVisualizationExplore = new Challenge(
    'Figure out the truly valuable data',
    [
        new TextContent(
            <p>
                There existed a legacy version of analysis functions but it was simply a copycat of Google Analytics. <strong>Undoubtedly Google Analytics is a successful product, yet we are building a chatbot platform, solving different problems and fulfilling different needs.</strong>
            </p>
        ),
        new ImgContent(
            legacy_analysis,
            'The legacy analysis feature was simply a copycat of Google Analytics.'
        ),
    ],
    [
        new TextContent(
            <p>We interviewed the sales team and users to figure out what kind of data and analysis are valuable for them and why.</p>
        ),
        new ImgContent(
            chart_research_table,
            'Exploring the data types that are truly valuable for users.'
        ),
        new TextContent(
            <p>Based on these observation and insights we could then proceed the design of data visualization.</p>
        ),
        new ImgContent(
            charts_design,
            'The data visualization design based on the observation and insights.'
        ),
    ],
);

const ChartLayout = new Challenge(
    `A responsive layout supports charts' dependency`,
    [
        new TextContent(
            <p>
                During the study above another important point got observed as well:
                Not every charts could be always accessible. It depends on the device a chatbot is building for, and / or the features a chatbot is using. <strong>Taking these dependencies into consideration added an extra layer of complexity on responsive layout design.</strong>
            </p>
        ),
    ],
    [
        new TextContent(
            <React.Fragment>
                <p>
                    Our first attempt was to manually precompose the charts based on their dependencies, yet this approach finally had been proved to be lack of flexibility and extendability.
                </p>
                <p>
                    Therefore <strong>we pivoted to a dynamic approach</strong>: Based on the chart type, complexity, data importance and dependency, we designed the max width, min width, stretchability and order for each chart card, and then fed those cards to a flex box.
                </p>
            </React.Fragment>
        ),
        new ImgContent(
            layout,
            'A flexible responsive layout for the charts.'
        ),
        new TextContent(
            <p>And to bring Pie chart better flexibility we created 2 types of design, a compact version and a loose one.</p>
        ),
        new ImgContent(
            pie_chart_types,
            'We made 2 types of design for Pie chart to guarantee its flexibility.'
        ),
    ],
);

const AudienceFlow = new Challenge(
    `Refactor the flow to meet the true needs.`,
    [
        new TextContent(
            <React.Fragment>
                <p>The tasks at first were about redesigning and updating the 3 existing features: Analysis, User List and Conversation History.</p>
                {/* <p>The requests at first came separately:</p>
                <ul>
                    <li><em>"We need a better analysis feature."</em></li>
                    <li><em>"We need a list includes all of the end users."</em></li>
                    <li><em>"We sometimes want to check the conversation history to better understand what happened."</em></li>
                </ul> */}
            </React.Fragment>
        ),
        new ImgContent(
            legacy_audience,
            'The legacy Analysis, User List and Conversation History features.'
        ),
        new TextContent(
            <React.Fragment>
                <p>Each of these design had bunch of problems not only visually but also functionally, <strong>yet some clues we observed were stopping us from simply redesigning them directly.</strong></p>
                <p>
                    First, behind these requests a same motivation was shared: studying the end users to optimize the chatbot as well as the business.
                </p>
                <p>
                    In addition, any one of the feature solely has limitations.
                </p>
                <ul>
                    <li>On one hand users would need to learn more details via user list and conversation history.</li>
                    <li>On the other hand touching user list or history directly would easily get users lost in the overwhelming data. Because not like social products, to the users of our service <strong>any end user is actually abstract and anonymous, only the activities can bring meaning and define the end user per se</strong>.</li>
                </ul>
            </React.Fragment>
        ),

    ],
    [
        new TextContent(
            <p>
                We reconsidered the relationship among those features and found that <strong>they were actually describing the end users’ behavior in different data scale and abstraction levels</strong>.
            </p>
        ),
        new ImgContent(
            radar_chart,
            'These features are actually describing the end users’ behavior in different data scale and abstraction levels.'
        ),
        new TextContent(
            <React.Fragment>
                <p>Start from this insight we transformed these 3 separate features to an united yet flexible flow where users could conveniently:</p>
                <ul>
                    <li>pick the proper method for reviewing and studying the audience based on the goals and data scale</li>
                    <li>define and optimize the scope of data based on the clues gained in the studying</li>
                    <li>iterate the process to finally get insights on business</li>
                </ul>
            </React.Fragment>
        ),
        new ImgContent(
            audience_flow,
            'A new united and flexible flow for studying end users.'
        ),
    ],
);

const BringContext = new Challenge(
    `Bring back the context`,
    [
        new TextContent(
            <p>
                <em>"To better study the behavior of end users, when reviewing history our users need to understand that among what candidates an option was finally picked."</em>
            </p>
        ),
        new TextContent(
            <p>
                To meet this demand the conversation history needs to provide context. On the other hand, as a material for browsing, good readability is essential as well. <strong>Thus the balance in-between became an important factor for the design of conversation history.</strong>
            </p>
        ),

    ],
    [
        new TextContent(
            <React.Fragment>
                <p>To figure out the balance point <strong>we classified the users according to the demand of context information</strong>.</p>
                <ul>
                    <li><strong>The informed</strong>: who either created the logic or interacted with it a lot therefore have the full story in their mind. In this case the context should stay “invisible” to guarantee good readability while majorly serve for bringing confidence or against a rainy day.</li>
                    <li><strong>The uninformed</strong>: who is not familiar with the chatbot structure thus context information is always highly needed for understanding the full story well.</li>
                </ul>
            </React.Fragment>
        ),
        new TextContent(
            <React.Fragment>
                <p>On top of these the principles were built:</p>
                <ul>
                    <li>Obvious difference should exist between regular contents and context info.</li>
                    <li>Context should have low visual weight. Visually it should be closer to background rather than regular contents.</li>
                    <li>Action needed for reviewing context should be as minimized as possible.</li>
                </ul>
            </React.Fragment>
        ),
        new ImgContent(
            context,
            'Bring back the context while keep readability.'
        ),
    ],
);

const Endurance = new Challenge(
    `Strengthen endurance in 2 levels`,
    [
        new TextContent(
            <p>
                In the product we use a variable-like name-value pair to allow storing and re-using data input by end users or sent from callback. Users could take advantage of it to trace their end users’ behaviors thus it was useful for analysis.
            </p>
        ),
        new TextContent(
            <React.Fragment>
                <p>This feature gives flexibility but also brings challenges:</p>
                <ul>
                    <li>Depending on the usage, business manner and culture, <strong>various cases could occur in the name or the value</strong>.</li>
                    <li>The importance and purpose of each name-value pair users created was hard to predicted. Thus <strong>it was hard to organize and priority them properly</strong>.</li>
                </ul>
            </React.Fragment>
        ),
    ],
    [
        new TextContent(
            <React.Fragment>
                <p>To face this challenge we:</p>
                <ul>
                    <li>Emphasize data endurance as a key point in the design.</li>
                    <li>Introduced Freeze Column feature to User List and Conversation History to provide familiar and flexible pattern for prioritizing information as needed.</li>
                </ul>
            </React.Fragment>
        ),
        new ImgContent(
            freeze,
            'Bring back the context while keep readability.'
        ),
    ]
);

const Mock = new Challenge(
    `Put the pieces together`,
    [
        new TextContent(
            <p>
                Solving those puzzles piece by piece and putting them together, the design was finally built up.
            </p>
        ),
        new ImgContent(
            [mock_analysis, mock_list, mock_conversation,],
            'Mockups for Analysis page, User List page and Conversation History modal.'
        ),
    ],
);


export const Audience = new CaseStudy(
    [
        AudienceFlow,
        DataVisualizationExplore,
        ChartLayout,
        BringContext,
        Endurance,
        Mock
    ]
);