import React from 'react';
import { CaseStudy, Challenge, TextContent, ImgContent } from '..';

import ani_basic from './ani_basic.gif';
import ani_broken from './ani_broken.gif';
import ani_def from './ani_def.gif';

import character_1 from './character_1.jpg';
import character_2 from './character_2.jpg';
import character_3 from './character_3.jpg';
import character_4 from './character_4.jpg';
import character_5 from './character_5.jpg';

import brainBottle_struct from './brainBottle_struct.jpg';
import brainBottle_sketch from './brainBottle_sketch.jpg';

import item_1 from './item_1.jpg';
import item_2 from './item_2.jpg';

import mock from './mock.jpg';

import block from './block.jpg';
import block_trick from './block_trick.jpg';

const juicyInteraction = new Challenge(
    'Make the advanture delighting and get players informed',
    [
        new TextContent(
            <p>
                With the design of animation and visual effect, we tried to bring players a juicy experience as well as communicate the situation of their character elegantly.
            </p>
        ),
        new ImgContent(
            [ani_broken, ani_basic, ani_def],
            'VFX designed for getting player informed.'
        ),
    ],
);

const blockDesign = new Challenge(
    'The challenges in the design of block',
    [
        new TextContent(
            <React.Fragment>
                <p>
                    The block plays a crucial part. It shapes the puzzles functionally and covers most of the viewport visually. Thus its design is extremely important for both game play and visual. But it might look simple at first, there exist a lot challenges.
                </p>
                <p>
                    First, as a rouge-like game, the landscape is structed by code logic and has endless compositions. Thus we deconstructed the block into 5 components, created assets for each one and finally assembled it in the runtime.
                </p>
            </React.Fragment>
        ),
        new ImgContent(
            block_trick,
            'Create endless landscape with just a few well designed assets.'
        ),
        new TextContent(
            <p>
                On the other hand, each type of block represent special envrionment while it per se is a puzzle. It may collapse easily or deadly toxic. The block design need take these into consideration and clearly communicate the game world as well as the gameplay.
            </p>
        ),
        new ImgContent(
            block,
            'The designs of block communicate both the game world and their own functionalities.'
        ),
    ]
)

const visualLanguage = new Challenge(
    'Build the visual style',
    [
        new TextContent(
            <p>
                Roge-like games are always considered as hardcore and minority, but the team hoped to bring it to the masses. Thus we explored and built a visual style which was casual yet not cheap.
            </p>
        ),
        new ImgContent(
            mock,
            'The semi-realistic UI creates a balance between hardcore and casual.'
        ),
        new ImgContent(
            [item_1, item_2],
            'The designs of item.'
        ),
        new ImgContent(
            [character_1, character_2, character_3, character_4, character_5],
            'The exploration and designs for some game charaters.'
        ),
        new ImgContent(
            [brainBottle_struct, brainBottle_sketch],
            'Build fun character.'
        ),
    ]
)

export const Dig = new CaseStudy(
    [
        blockDesign,
        juicyInteraction,
        visualLanguage,
    ]
);